
import {Container, Row} from 'react-bootstrap';
import { LinkContainer } from 'react-router-bootstrap';
import { Example } from '../component/example/example';
import { Examples } from '../component/example/examples';
import { GD } from '../component/inline/inline-helper-gd';

/**
 * Page for the idiom "didheil air (keen on)"
 */
export function IdiomKeenOn() {
    return <Container>
        <Row>
            <h1>didheil air (keen on)</h1>
            <p>This idiom combines the adjective <GD>didheil</GD> (desirous of, eager for, fond of, keen on), with the prepostion{' '}
                <LinkContainer to="/grammar/preposition/air(on)"><a>
                    <GD>air</GD> (on)
                </a></LinkContainer>, to mean keen on or fond of something.
            </p>
            <p>
                <Example
                    gaelic="Tha Anna didheil air cel"
                    english="Anna is keen on music"
                ></Example>
            </p>
            <p>
                <ol>
                    <li>Begin with some form of <GD>bi</GD>, for example <GD><strong>tha</strong></GD></li>
                    <li>Next is the subject, for example <GD><strong>Anna</strong></GD></li>
                    <li>Next is the adjective <GD><strong>didheil</strong></GD></li>
                    <li>Next is the preposition <GD><strong>air</strong></GD></li>
                    <li>Next is the object of the desire, for example <GD><strong>cel</strong></GD></li>
                </ol>
            </p>
            
            <h2>Examples</h2>
            <p>
                <Examples>
                    <Example
                        gaelic="Tha mi didheil air Calum"
                        english="I am keen on Calum"
                    ></Example>
                    <Example
                        gaelic="Bha iad didheil air buntta"
                        english="They were keen on a potato"
                    ></Example>
                    <Example
                        gaelic="Bidh mi didheil air leann"
                        english="I will be keen on a beer"
                    ></Example>
                </Examples>
            </p>

            <h2>Pronoun Examples</h2>
            <p>
                When the object being desired is a pronoun, the pronominal form of{' '}
                <LinkContainer to="/grammar/preposition/air(on)"><a>
                    <GD>air</GD> (on)
                </a></LinkContainer> should be used.
            </p>
            <p>
                <Examples>
                    <Example
                        gaelic="Tha Anna didheil orra"
                        english="Anna is keen on them"
                    ></Example>
                    <Example
                        gaelic="Tha i didheil air"
                        english="She is keen on him"
                    ></Example>
                    <Example
                        gaelic="Tha e didheil orm"
                        english="He is keen on me"
                    ></Example>
                </Examples>
            </p>
        </Row>
    </Container>
}